
@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700");
.popup.popup-member{font-family:"Roboto Condensed",sans-serif;height:500px;position:absolute;top:50% !important;margin-top:0 !important;-khtml-transform:translate3d(0,0,0) translateY(-50%)!important;transform:translate3d(0,0,0) translateY(-50%)!important
}
.popup.popup-member.modal-out{-khtml-transform:translate3d(0,1px,0);transform:translate3d(0,1px,0);opacity:0;display:none !important
}
.popup.popup-member .navbar{background:#e91e63
}
.popup.popup-member .navbar .navbar-inner{padding:0 25px
}
.popup.popup-member .navbar .navbar-inner .center{font-size:16px;font-weight:500;text-transform:uppercase
}
.popup.popup-member .popover-content{height:calc( 100% - 56px - 48px);overflow:auto
}
.popup.popup-member .popover-content .list-block{margin:0px
}
.popup.popup-member .toolbar{width:100%;font-size:14px;text-align:center;background:white
}
.popup.popup-member .toolbar .link{width:100%
}
.popup.popup-member .toolbar .icon{padding-left:10px;font-size:16px;color:white
}
.popup.popup-member .toolbar .button{padding-left:20px;padding-right:20px;background:#e91e63;margin:auto
}
.popup.popup-member .fb-friend{display:inline-block;width:100%;padding:0 25px;vertical-align:top;color:black;position:relative
}
.popup.popup-member .fb-friend>div{padding:10px 0;border-bottom:1px solid #dfdfdf
}
.popup.popup-member .fb-friend.fb-friend-input{padding-top:10px
}
.popup.popup-member .fb-friend.fb-friend-input>div{padding:0px;border:0px
}
.popup.popup-member .fb-friend .f7-icons{position:absolute;top:20px;left:38px;line-height:50px;width:50px;border-radius:100%;color:rgba(255,255,255,0);font-size:22px
}
.popup.popup-member .fb-friend .f7-icons:hover{background:rgba(0,0,0,0.5);color:rgba(255,255,255,0.4)
}
.popup.popup-member .fb-friend .fb-friend-email,.popup.popup-member .fb-friend .fb-friend-contact{font-size:12px;color:gray
}



