
.tree .card{margin:0;background:none;border-radius:10px;overflow:hidden;display:inline-block;width:100%;background:#1b1b1b;min-height:111px;max-height:111px
}
.tree .card .card-header{padding:10px;background-size:cover;background-position:center center;display:inline-block;width:100%
}
.tree .card .card-header .image{border-radius:100%;height:50px;width:50px;background-size:cover;float:right;position:relative;z-index:2
}
.tree .card .card-header .image.placeholder{position:absolute;right:10px;z-index:1
}
.tree .card .card-header .title{border:0;line-height:20px;padding:5px 10px;border:0;color:white;font-weight:300;font-size:16px;padding-left:0px;padding-right:60px;box-sizing:border-box;border-radius:0 10px 10px 0;text-align:left
}
.tree .card .card-header .age{font-size:12px;color:#747474;text-align:left
}
.tree .card .card-header::after{display:none
}
.tree .card .card-footer{padding:0 7px;background:#1b1b1b;margin-top:-1px;min-height:0px
}
.tree .card .card-footer::before{display:none
}
.tree .card .card-footer a{color:white;width:auto;min-width:100%;height:auto
}
.tree .card .card-footer a:active{opacity:0.3
}
.node{width:180px;margin:auto;display:inline-block;padding:0 10px;position:relative
}
.node.partners:nth-of-type(1):after{content:'';position:absolute;top:50%;right:-0.5px;border-top:1px solid #ccc;width:11px;height:0
}
.node.partners:nth-of-type(2):after{content:'';position:absolute;top:50%;left:-1px;border-top:1px solid #ccc;width:11px;height:0
}
.node.partners.open:nth-of-type(1):before{content:'';position:absolute;top:50%;right:-0.5px;border-right:1px solid #ccc;width:0;height:50%
}
.node.partners:not(.root):nth-of-type(2):before{content:'';position:absolute;top:0;left:-0px;border-right:1px solid #ccc;width:0;height:50%
}



