
.tree{text-align:center;overflow:auto;height:100%
}
.tree ul{padding-top:20px;position:relative;padding-left:0px
}
.tree li{float:left;text-align:center;list-style-type:none;position:relative;padding:20px 5px 0
}
.tree li::before{content:'';position:absolute;top:0;right:50%;border-top:1px solid #ccc;width:50%;height:20px
}
.tree li::after{content:'';position:absolute;top:0;right:50%;border-top:1px solid #ccc;width:50%;height:20px;right:auto;left:50%;border-left:1px solid #ccc
}
.tree li:only-child{padding-top:0
}
.tree li:only-child::after,.tree li:only-child::before{display:none
}
.tree li:first-child::before{border:0 none
}
.tree li:last-child::after{border:0 none
}
.tree li:last-child::before{border-right:1px solid #ccc;border-radius:0 5px 0 0;-webkit-border-radius:0 5px 0 0;-moz-border-radius:0 5px 0 0
}
.tree li:first-child::after{border-radius:5px 0 0 0;-webkit-border-radius:5px 0 0 0;-moz-border-radius:5px 0 0 0
}
.tree li:only-child{left:50%;transform:translateX(-50%)
}
.tree>ul{width:5000px;height:100%;padding:0px;padding-top:60px;transform-origin:center top
}
.tree>ul>li{margin-bottom:100px
}
.tree ul ul::before{content:'';position:absolute;top:0;left:calc( 50% - 0.5px);border-left:1px solid #ccc;width:0;height:20px
}
.tree li a{text-decoration:none;color:#666;font-family:arial, verdana, tahoma;font-size:11px;display:inline-block;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px
}
ul:not(.md-list)>li+li{margin-top:0
}



